/* eslint-disable jsx-a11y/anchor-is-valid */
import { useState, useEffect } from "react"
import { inject, observer } from 'mobx-react';
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import Button from "../../components/Button/Button"
import ApiProxy from '../../model/ApiProxy'
import Icon from "../../components/Icon/Icon";
import useConfig from "../../hooks/useConfig"
import { PRIMARY } from '../../utils/Constants';
import deriLogo from "../../assets/img/logo.svg"
import pancake from "./img/pancake.svg"
import sushi from "./img/sushiswap-sushi-logo.svg"
import gate from "./img/gateio.svg"
import mexc from "./img/mxc.svg"
import governance from './img/governance.svg'
import privilege from './img/privilege.svg'
import './token.less'
function Token({ wallet = {}, lang }) {
  const [deriTokenAddress, setDeriTokenAddress] = useState()
  const [deriInfo, setDeriInfo] = useState()
  const [isFaqOpen, setFaqOpen] = useState(false)
  const [burnedAomunt, setBurnedAomount] = useState()
  const config = useConfig();
  const faqList = [
    { label: lang['what-is-deri'], labelDes: lang['what-is-deri-des'] },
    { label: lang['is-deri-erc20-token'], labelDes: lang['is-deri-erc20-token-des'] },
    { label: lang['what-deri-token-total'], labelDes: lang['what-deri-token-total-des'], link: 'https://docs.deri.io/library/tokenomics', linkLabel: lang['more'] },
  ]
  const addToken = async () => {
    if (deriTokenAddress) {
      const tokenSymbol = 'DERI';
      const tokenDecimals = 18;
      try {
        const wasAdded = await window.ethereum.request({
          method: 'wallet_watchAsset',
          params: {
            type: 'ERC20', // Initially only supports ERC20, but eventually more!
            options: {
              address: deriTokenAddress, // The address that the token is at.
              symbol: tokenSymbol, // A ticker symbol or shorthand, up to 5 chars.
              decimals: tokenDecimals, // The number of decimals in the token
            },
          },
        });

        if (wasAdded) {
          console.log('Thanks for your interest!');
        } else {
          console.log('Your loss!');
        }
      } catch (error) {
        console.log(error);
      }
    }
  }
  const getDeriInfo = async () => {
    let path = `${process.env.REACT_APP_INFO_HTTP_URL}/stats_for_token`
    let resp = await fetch(path);
    let res = await resp.json();
    if (res) {
      let obj = {}
      obj.price = res.data.price
      obj.market_cap = res.data.market_cap
      obj.circulating_supply = parseInt(res.data.circulating_supply)
      obj.total_supply = parseInt(res.data.total_supply)
      setDeriInfo(obj)
    }

  }

  const getBurned = async () => {
    let res = await ApiProxy.request("getDeriBurnedAmount")
    setBurnedAomount(res)
  }

  const sushiBuy = async () => {
    if (wallet.isConnected()) {
      await wallet.switchNetwork(config[1])
      window.open("https://app.sushi.com/swap?inputCurrency=&outputCurrency=0xA487bF43cF3b10dffc97A9A744cbB7036965d3b9")
    } else {
      window.open("https://app.sushi.com/swap?inputCurrency=&outputCurrency=0xA487bF43cF3b10dffc97A9A744cbB7036965d3b9")
    }
  }

  const pancakeBuy = async () => {
    if (wallet.isConnected()) {
      await wallet.switchNetwork(config[56])
      window.open("https://exchange.pancakeswap.finance/#/swap?inputCurrency=0xe60eaf5a997dfae83739e035b005a33afdcc6df5")
    } else {
      window.open("https://exchange.pancakeswap.finance/#/swap?inputCurrency=0xe60eaf5a997dfae83739e035b005a33afdcc6df5")
    }
  }
  useEffect(() => {
    if (wallet.isConnected()) {
      let address;
      if (+wallet.detail.chainId === 56) {
        address = '0xe60eaf5A997DFAe83739e035b005A33AfdCc6df5'
      } else if (+wallet.detail.chainId === 1) {
        address = '0xa487bf43cf3b10dffc97a9a744cbb7036965d3b9'
      } else if (+wallet.detail.chainId === 128) {
        address = '0x2bdA3e331Cf735D9420e41567ab843441980C4B8'
      } else if (+wallet.detail.chainId === 137) {
        address = '0x3d1d2afd191b165d140e3e8329e634665ffb0e5e'
      }
      setDeriTokenAddress(address)
    }

  }, [wallet.detail])
  useEffect(() => {
    let interval = null;
    //计数器
    interval = window.setInterval(() => {
      getDeriInfo()
    }, 300000);
    getDeriInfo()
    getBurned()
    return () => {
      interval && clearInterval(interval);
    };
  }, [])
  return (
    <div className='token-box'>
      <div className='token-info'>
        <div className='token-header'>
          <div className='token-header-left'>
            <div className='logo-title'>
              <img alt='' src={deriLogo} />
              <span className='title-text'>{lang['title']}</span>
            </div>
            <div className='token-header-des'>
              {lang['deri-is-the-protocol-token']}
              <a rel='noopener noreferrer' href='https://docs.deri.io/tokenomics' target='_blank' className='learn-more'> {lang['learn-more']} </a>
            </div>
          </div>
          <div className='token-header-right'>
            <Button onClick={() => { addToken() }} type={PRIMARY} width="242" label={lang["add-deri-to-metamask"]} />
          </div>
        </div>
        <div className='token-info-content'>
          <div className='top-info-box'>
            <div className='info-box'>
              <div className='num'>
                $ {deriInfo ? <DeriNumberFormat value={deriInfo.price} displayType='text' thousandSeparator={true} decimalScale={4} /> : '--'}
              </div>
              <div className='text'>
                {lang['price']}
              </div>
            </div>
            <div className='info-box min-box'>
              <div className='num'>
                $ {deriInfo ? <DeriNumberFormat value={deriInfo.market_cap} displayType='text' thousandSeparator={true} decimalScale={0} /> : '--'}
              </div>
              <div className='text'>
                {lang['market-cap']}
              </div>
            </div>
          </div>
          <div className='buttom-info-box'>
            <div className='info-box'>
              <div className='num'>
                {deriInfo ? <DeriNumberFormat value={deriInfo.circulating_supply} displayType='text' thousandSeparator={true} decimalScale={0} /> : '--'}
              </div>
              <div className='text'>
                {lang['circulating-supply']}
              </div>
            </div>
            <div className='info-box min-box'>
              <div className='num'>
                {deriInfo ? <DeriNumberFormat value={deriInfo.total_supply} displayType='text' thousandSeparator={true} decimalScale={0} /> : '--'}
              </div>
              <div className='text'>
                {lang['total-supply']}
              </div>
            </div>
          </div>
        </div>
        <div className='buy-deri-token'>
          {lang["token-burning"]}
        </div>

        <div className='token-info-content token-burning-content'>
          <div className='top-info-box  token-burning'>
            <div className='info-box'>
              <div className='num'>
                4,357,036
              </div>
              <div className='text'>
                {lang['burned-to-mining-vault']}
              </div>
            </div>
          </div>
          <div className='buttom-info-box  token-burning'>
            <div className='info-box'>
              <div className='num'>
                <DeriNumberFormat value={burnedAomunt} displayType='text' thousandSeparator={true} decimalScale={0} />  <a target="_blank" rel="noreferrer" href="https://etherscan.io/token/0xa487bf43cf3b10dffc97a9a744cbb7036965d3b9?a=0x000000000000000000000000000000000000dead"> <Icon token="target" /></a>
              </div>
              <div className='text'>
                {lang['burned-to-deadlock-address']}
              </div>
            </div>
          </div>
        </div>
        <div className='buy-deri-token'>
          {lang["buy-deri-token"]}
        </div>
        <div className='buy-deri-token-swap'>
          <a target='_blank' onClick={pancakeBuy}>
            <div className='swap-box'>
              <div className='swap-logo'>
                <img alt='' src={pancake} />
              </div>
              <div className='swap-name'>
                {lang['pancake-swap']}
              </div>
            </div>
          </a>
          <a target='_blank' onClick={sushiBuy}>
            <div className='swap-box'>
              <div className='swap-logo'>
                <img alt='' src={sushi} />
              </div>
              <div className='swap-name'>
                {lang['sushi-swap']}
              </div>
            </div>
          </a>
          <a target='_blank' rel='noreferrer' href='https://www.gate.io/tradepro/DERI_USDT'>
            <div className='swap-box'>
              <div className='swap-logo'>
                <img alt='' src={gate} />
              </div>
              <div className='swap-name'>
                {lang['gate']}
              </div>
            </div>
          </a>
          <a target='_blank' rel='noreferrer' href='https://www.mexc.com/exchange/DERI_USDT'>
            <div className='swap-box'>
              <div className='swap-logo'>
                <img alt='' src={mexc} />
              </div>
              <div className='swap-name'>
                {lang['mexc']}
              </div>
            </div>
          </a>
        </div>
        <div className='benefits-faq-box'>
          <div className='benefits-box'>
            <div className='benefits-title'>
              {lang['benefits-of-deri-token']}
            </div>
            <div className='benefits-info'>
              <div className='governance'>
                <img src={governance} alt='' />
                <div className='governance-des'>
                  <div className='governance-title'>
                    {lang['governance']}
                  </div>
                  <div className='governance-text'>
                    {lang['governance-des']}
                  </div>
                </div>
              </div>
              <div className='privilege'>
                <img src={privilege} alt='' />
                <div className='privilege-des'>
                  <div className='privilege-title'>
                    {lang['privilege']}
                  </div>
                  <div className='privilege-text'>
                    {lang['privilege-des']}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className='faq-box'>
            <div className='faq-title'>
              {lang['faq']}
            </div>
            <div className='faq-info'>
              {faqList.map((item, index) => {
                return (
                  <FaqInfo item={item} index={index} />
                )
              })}
              <div className='faq-info-box' >
                <div className='faq-title-img' onClick={() => { setFaqOpen(!isFaqOpen) }}>
                  <div className='faq-question-title'>
                    {lang['what-are-the']}
                  </div>
                  <div>
                    <Icon token={isFaqOpen ? 'arrow-up' : 'arrow-down'} width='20' height='20' />
                  </div>
                </div>
                {isFaqOpen && <div className='faq-des'>
                  <div className='governance-privilege'>
                    {lang['governance']}
                  </div>
                  <div>
                    {lang['what-are-the-govenance-des']}
                  </div>
                  <div className='governance-privilege privilege'>
                    {lang['privilege']}
                  </div>
                  <div>
                    {lang['what-are-the-privilege-des-one']}
                  </div>
                  <div>
                    <br></br>
                    {lang['what-are-the-privilege-des-two']}
                  </div>
                  <div className='specifically'>
                    {lang['what-are-the-privilege-des-three']}
                  </div>
                  <div className='specifically-des'>
                    <div className='list-style'></div>
                    <div>
                      {lang['what-are-the-privilege-des-three-des-one']}
                    </div>
                  </div>
                  <div className='specifically-des'>
                    <div className='list-style'></div>
                    <div>
                      {lang['what-are-the-privilege-des-three-des-two']}
                    </div>
                  </div>
                  <div className='specifically'>
                    {lang['what-are-the-privilege-des-four']}
                  </div>
                </div>}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

function FaqInfo({ item, index }) {
  const [isFaqOpen, setFaqOpen] = useState(false)
  return (
    <div className='faq-info-box' key={index}>
      <div className='faq-title-img' onClick={() => { setFaqOpen(!isFaqOpen) }} >
        <div className='faq-question-title'>
          {item.label}
        </div>
        <div>
          <Icon token={isFaqOpen ? 'arrow-up' : 'arrow-down'} width='20' height='20' />
        </div>
      </div>
      {isFaqOpen && <div className='faq-des'>
        {item.labelDes}
        {item.link && <a rel="noreferrer" target="_blank" href={item.link}> {item.linkLabel}</a>}
      </div>}
    </div>
  )

}
export default inject('wallet')(observer(Token))